<template>
    <div>
        <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
            <el-breadcrumb-item>权限管理</el-breadcrumb-item>
            <el-breadcrumb-item>角色列表</el-breadcrumb-item>
        </el-breadcrumb>
        <el-card>
             <el-button type="primary">添加角色</el-button>
            <el-table :data="rolesList" border stripe row-key="id">
                <el-table-column type="expand">
                    <template slot-scope="scope">
                        <el-row :class="['bdbottom',i1 === 0 ? 'bdtop' : '' ,'vcenter']" v-for="(item1,i1) in scope.row.children" :key="item1.id">
                            <el-col :span="5">
                                <el-tag type="success" closable @close="removeRightbyid(scope.row,item1.id)">{{i1 +1}}.{{item1.authName}}</el-tag>
                                <i class="el-icon-caret-right"></i>
                            </el-col>
                            <el-col :span="19">
                                <el-row :class="[i2===0?'':'bdtop','vcenter']" v-for="(item2,i2) in item1.children" :key="item2.id">
                                    <el-col :span="6">
                                        <el-tag type="warning" closable @close="removeRightbyid(scope.row,item2.id)">{{i1 +1}}.{{i2 +1}}{{item2.authName}}</el-tag>
                                        <i class="el-icon-caret-right"></i>
                                    </el-col>
                                    
                                    <el-col :span="18">
                                        <el-row v-for="(item3,i3) in item2.children" :key="item3.id" >
                                            <el-col>
                                                <el-tag type="danger" closable @close="removeRightbyid(scope.row,item3.id)">{{i1 +1}}.{{i2 +1}}.{{i3 +1}}{{item3.authName}}</el-tag>
                                            </el-col>
                                        </el-row>
                                    </el-col>
                                </el-row>
                            </el-col>
                        </el-row>
                    </template>
                </el-table-column>
                <el-table-column type="index"></el-table-column>
                 <el-table-column label="角色名称" prop="roleName"></el-table-column>
                <el-table-column label="角色描述" prop="roleDesc"></el-table-column>
                <el-table-column label="角色操作">
                    <template slot-scope="scope">
                        <el-button type="primary" icon="el-icon-edit" >编辑</el-button>
                   <el-button type="danger" icon="el-icon-delete" @click="removeroles(scope.row.id)">删除</el-button>
                   <el-button type="warning" icon="el-icon-setting" @click="showsetright(scope.row)">分配权限</el-button>
                    </template>
                   
                </el-table-column>
            </el-table>
        </el-card>
        <el-dialog
            title="分配权限"
            :visible.sync="SetRightdialogVisible"
            width="50%"
            @close = "closedialog">
            <el-tree   :data="righttree" :props="defaultProps" show-checkbox 
            node-key="id" ref="treeRef"
            default-expand-all :default-checked-keys="defkeys"></el-tree>
            <span slot="footer" class="dialog-footer">
                <el-button @click="SetRightdialogVisible = false">取 消</el-button>
                <el-button type="primary" @click="allotRights">确 定</el-button>
            </span>
        </el-dialog>
    </div>
</template>
<script>
export default {
    data() {
        return {
            rolesList:[],
            SetRightdialogVisible:false,
            righttree:[],
            defaultProps:{
                children:'children',
                label:'authName'
            },
            defkeys:[],
            roleId:[]
        }
    },
    created() {
        this.getrolesList()
    },
    methods: {
        async getrolesList(){
            const {data:res} = await this.$http.get('roles')
            if (res.meta.status!==200) {
                return this.$message.error("加载角色失败")
            }
            this.rolesList = res.data
            console.log(this.rolesList);
        },
        async removeroles(id){
             const confirmResult= await this.$confirm('此操作将永久删除该角色, 是否继续?', '提示', {
            
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
            }).catch(err=> err)
            // console.log(confirmResult)
            if (confirmResult!=='confirm') {
                return this.$message.info('已经取消删除')
            }
            const{data:res} =  await this.$http.delete('roles/'+id )
            if(res.meta.status!==200){
                return this.$message.error('删除角色失败')
            }
            this.$message.success('删除角色成功')
            this.getrolesList()
        },
        async removeRightbyid(role,id){
            const confirmResult= await this.$confirm('此操作将永久删除该角色, 是否继续?', '提示', {
            
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
            }).catch(err=> err)
            // console.log(confirmResult)
            if (confirmResult!=='confirm') {
                return this.$message.info('已经取消删除')
            }
            const{data:res} =  await this.$http.delete(`roles/${role.id}/rights/${id}` )
            if(res.meta.status!==200){
                return this.$message.error('删除失败')
            }
            this.$message.success('删除成功')
            role.children = res.data
        },
        async showsetright(role){
            this.roleId = role.id
          const {data:res}= await this.$http.get('rights/tree')
          if (res.meta.status!==200) {
              return this.$message.error("获取权限数据失败")
          }
          this.righttree = res.data
          this.getleafkeys(role,this.defkeys)
        this.SetRightdialogVisible = true;
        },
        getleafkeys(node,arr){
            if(!node.children){
                return arr.push(node.id)
            }
            node.children.forEach(item => {
                this.getleafkeys(item,arr)
            });
        },
        closedialog(){
            this.defkeys = [];
        },
        async allotRights(){
            const keys =[...this.$refs.treeRef.getCheckedKeys(),
            ...this.$refs.treeRef.getHalfCheckedKeys()];
            const idStr = keys.join(',')
          const {data:res} =  await this.$http.post(`roles/${this.roleId}/rights`,{rids:idStr})
           if (res.meta.status!==200) {
               return this.$message.error("分配权限失败")
           }
           this.$message.success("分配权限成功")
           this.getrolesList()
           this.SetRightdialogVisible = false;
           console.log(keys);
        }
    },
}
</script>
<style lang="less" scoped>
.el-tag{
    margin: 7px;
}
.bdtop{
    border-top: 1px solid #eeee;
}
.bdbottom{
    border-bottom: 1px solid #eeee;
}
.vcenter{
    display: flex;
    align-items: center;
}
</style>